<template><section><html><head></head><body><h1>SingleTileImageryProvider</h1>
<p>The <code v-pre="">vc-provider-imagery-tile-single</code> component is used to load a single image as the image basemap. It only supports latitude and longitude projection. The aspect ratio of the image is preferably 2:1, otherwise there will be stretching.</p>
<h2>Example</h2>
<h3>Load an imagerylayer with SingleTileImageryProvider</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tile-single :url="url"></vc-provider-imagery-tile-single>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
